import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import LanguageDetector from 'i18next-browser-languagedetector';
import { InitOptions } from 'i18next';
// src
import enTranslation from './locales/en/translation.json';
import zhTranslation from './locales/zh/translation.json';

// GitHub Pages
const isGitHubPages = window.location.hostname.includes('github.io');

//  - 
const basePath = isGitHubPages ? '' : '';

// 
const initOptions: InitOptions = {
  fallbackLng: 'en',
  supportedLngs: ['en', 'zh'],
  debug: process.env.NODE_ENV === 'development',
  
  // HTTP
  resources: {
    en: {
      translation: enTranslation
    },
    zh: {
      translation: zhTranslation
    }
  },
  
  interpolation: {
    escapeValue: false, // React  XSS 
  },
  
  detection: {
    // 
    order: ['navigator', 'querystring', 'localStorage', 'cookie', 'htmlTag'],
    
    // 
    lookupQuerystring: 'lng',
    lookupCookie: 'i18next',
    lookupLocalStorage: 'i18nextLng',
    
    // 
    caches: ['localStorage', 'cookie'],
  },
  
  react: {
    useSuspense: false, // Suspense
  },
};

i18n
  // 
  .use(LanguageDetector)
  // i18nreact-i18next
  .use(initReactI18next)
  // i18next
  .init(initOptions);

// 
const detectAndSetLanguage = () => {
  //  'zh-CN'  'en-US'
  const browserLang = navigator.language;
  //  'zh'  'en'
  const langCode = browserLang.split('-')[0];
  
  // 
  if (['en', 'zh'].includes(langCode)) {
    i18n.changeLanguage(langCode);
    console.log(`Language set to ${langCode} based on browser settings`);
  }
};

// DOM
detectAndSetLanguage();
window.addEventListener('DOMContentLoaded', detectAndSetLanguage);

export default i18n; 